import React, {useState} from "react";
import api from "../../utils/api";
import { useAlert } from 'react-alert'

import GoBack from "../ui/GoBack";

import {useParams} from "react-router-dom";

const ResetPassword = () => {
  const alert = useAlert()
  const {user, type} = useParams();

  const [oldPassword, setOldPassword] = useState()
  const [password, setPassword] = useState()
  const [passwordConfirm, setPasswordConfirm] = useState()

  const handleSubmit = async () => {
    if (password !== passwordConfirm) {
      return alert.show('  ')
    }
    try {
      await api.post(`/${type}/set-password`, {login: user, oldPassword, password});
      alert.show('  ')

    } catch (e) {
      e.response.data.errors.forEach(err => {
        alert.show(err.msg)
      })
    }
  }

  return (
    <div className={"menu-bg"} id={'resetPassForm'}>
      <div className={"menu-wrapper"}>
        <form className='form' onSubmit={(e) => handleSubmit(e)}>
          <div className="login-header">
            <p><span className='golden-compass'><span className='g-letter'></span></span> </p>
          </div>
          <div className={'inputs-wrapper'}>
            <div className="form-group field">
              <input type="password" className="form-field" placeholder="  " name="oldPassword"
                     id='oldPassword'
                     onChange={e => setOldPassword(e.target.value)} required/>
              <label htmlFor="oldPassword" className="form-label">  </label>
            </div>

            <div className="form-group field">
              <input type="password" className="form-field" placeholder="  " name="password"
                     id='password'
                     onChange={e => setPassword(e.target.value)} required/>
              <label htmlFor="password" className="form-label">  </label>
            </div>

            <div className="form-group field">
              <input type="password" className="form-field" placeholder="   "
                     name="passwordConfirm"
                     id='passwordConfirm'
                     onChange={e => setPasswordConfirm(e.target.value)} required/>
              <label htmlFor="passwordConfirm" className="form-label">   </label>
            </div>

            <div className='submit-btn-wrapper'>
              <button type="submit" className='btn' id='loginBtn'>
                <span> </span>
              </button>
            </div>
          </div>
        </form>
        <GoBack/>

      </div>
    </div>
  );
}

export default ResetPassword;